/* ===========================================================

  # Icons

=========================================================== */

.u-icon {
	fill: $pri;
}

.u-icon--invert {
	fill: $white;
}

.u-icon--16 {
	width: 1.6rem;
	height: 1.6rem;
}

.u-icon--24 {
	width: 2.4rem;
	height: 2.4rem;
}

.u-icon--32 {
	width: 3.2rem;
	height: 3.2rem;
}
